<!-- 悬浮按钮滚动到顶部 -->
<template>
	<view :animation="animationData" class="btn-plusempty" hover-class="bottom-btn-hover" @tap="tapTop">
		<text class="my-icon plusempty-icon">&#xe66c;</text>
	</view>
</template>

<script>
	let animation = uni.createAnimation({
		timingFunction: 'ease',
	})
	export default {
		props: {
			// 页面滚动距离
			scrollTop: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				animationData: {}
			}
		},

		onLoad() {

		},
		created() {
			//默认不显示
			animation.opacity(0).step({duration:0});

			this.animationData = animation.export()
		},
		watch: {
			scrollTop(newVal, oldVal) {
				if (newVal < 100) {
					//淡出
					animation.opacity(0).step({duration:800})
					this.animationData = animation.export()
				} else {
					//淡入
					animation.opacity(1).step({duration:800}) //描述动画
					this.animationData = animation.export()
				}
			}
		},
		/**
		 * 计算属性
		 */
		computed: {

		},
		methods: {
			//滚动到顶部
			tapTop() {
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 500
				});
			}

		}
	}
</script>

<style scoped>
	@font-face {
	    font-family: 'wpfont';
	    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2');
	    font-weight: normal;
	    font-style: normal;
	    font-display: swap;
	}
	
	.my-icon {
		font-family: wpfont;
	}
	.btn-plusempty {
		width: 80upx;
		height: 80upx;
		background: #0fa3fa;
		position: fixed;
		bottom: 80upx;
		right: 40upx;
		border-radius: 100%;
		overflow: hidden;
		text-align: center;
		line-height: 80upx;
		border: 4upx solid #b1def9;
	}

	.bottom-btn-hover {
		background: #0fa3fa !important;
	}

	.plusempty-icon {
		color: #fff;
		font-size: 36upx;
		font-weight: bold;
	}
</style>
